@import url("https://fonts.googleapis.com/css2?family=Dongle:wght@300;400&display=swap");

@keyframes increaseHeight {
  from {
    width: 0;
   
  }


  to {
    width: 300px;
    
  }
}

@keyframes expecont {
  from{
    display: none;
  }
  to{
    display: block;
  }
}

@keyframes displayExpe {
  from {
    position: absolute;
    left: 30%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px;
    border: solid 1px rgb(241, 239, 237);
    font-size: 20px;
    width: 62%;
    font-weight: bold;
    opacity: 0;
    transform: scale(0);
  }

  to {
    position: absolute;
    left: 30%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px;
    border: solid 1px rgb(241, 239, 237);
    font-size: 20px;
    width: 62%;
    font-weight: bold;
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeimg {
  from {
    display: none;
    opacity: 0;
    transform: translateX(3000px);
    
  }
  to {
    display: block;
    opacity: 1;
    transform: translateX(0);
   
   
  }
}

@keyframes increaseBranchWidth {
  from {
    height: 5px;
    width: 0px;
   
    background-color: rgb(248, 249, 251);
  }
  to {
    height: 5px;
    width: 105px;
    background-color: rgb(255, 255, 255);
  }
}

@keyframes increaseTroncHeight {
  from {
    height: 0;
    width: 5px;
    background-color: rgb(247, 249, 250);
  }
  to {
    height: 1000px;
    width: 5px;
    background-color: rgb(248, 248, 248);
  }
}

@keyframes fadeMenuContent {
  from {
    transform: translateX(-700px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes defadeMenuContent {
  from {
    transform: translateX(0);

  }
  to {
    transform: translateX(-700px);

  }
}

@keyframes increaseLvl1 {
  
  from {
    width: 0%;
    
  }


  to {
    width: 77%;
   
  }
}

@keyframes increaseLvl2 {
  
  from {
    width: 0%;
    
  }


  to {
    width: 86%;
   
  }
}

@keyframes increaseLvl3 {
  
  from {
    width: 0%;
    
  }


  to {
    width: 90%;
   
  }
}
@keyframes increaseLvl4 {
  
  from {
    width: 0%;
    
  }


  to {
    width: 76%;
   
  }
}

@keyframes increaseLvl5 {
  
  from {
    width: 0%;
    
  }


  to {
