  }
}

@keyframes increaseLvl6 {
  
  from {
    width: 0%;
    
  }


  to {
    width: 80%;
   
  }
}

@keyframes changecolorhover {
   from {
    border: solid 1px rgb(241, 239, 237);
   }
   to {
    border: solid 1px rgb(227, 115, 3);
   }
}

@keyframes increaseLvl7 {
  
